@import url(https://fonts.googleapis.com/css?family=Rubik:400,700,900&display=swap);
@import url(https://afeld.github.io/emoji-css/emoji.css);
body {
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }
  @media only screen and (max-width: 43.75em) {
    html {
      font-size: 50%; } }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Rubik', sans-serif;
  color: #9d9999;
  height: 100vh;
  background: -webkit-linear-gradient(315deg, #b3e7fc 0%, #b3e7fc 50%, #f25aa3 50%, #f25aa3 100%);
  background: linear-gradient(135deg, #b3e7fc 0%, #b3e7fc 50%, #f25aa3 50%, #f25aa3 100%); }

.JokeList {
  display: flex;
  width: 80%;
  height: 80%; }
  @media only screen and (max-width: 37.5em) {
    .JokeList {
      width: 100%;
      height: 100%; } }
  @media only screen and (max-width: 31.25em) {
    .JokeList {
      flex-direction: column; } }
  .JokeList__jokes {
    height: 90%;
    width: 75%;
    border-radius: 3px;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-self: center;
    overflow: scroll;
    box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.6); }
    @media only screen and (max-width: 37.5em) {
      .JokeList__jokes {
        height: 100%; } }
    @media only screen and (max-width: 31.25em) {
      .JokeList__jokes {
        width: 100%;
        z-index: 3; } }
    .JokeList__jokes-joke:not(:last-child) {
      border-bottom: 2px solid #eeeeee; }
  .JokeList__sidebar {
    background-color: #b26fd3;
    width: 25%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    box-shadow: 0 1.5rem 3rem rgba(0, 0, 0, 0.4), 0 2rem 3rem inset rgba(0, 0, 0, 0.2);
    border-radius: 3px;
    z-index: 2; }
    @media only screen and (max-width: 31.25em) {
      .JokeList__sidebar {
        width: 100%;
        flex-direction: row;
        justify-content: space-around; } }
    .JokeList__sidebar-title {
      font-size: 4rem;
      margin: 2rem 0;
      color: white;
      font-weight: 100;
      letter-spacing: .6rem; }
      .JokeList__sidebar-title span {
        font-weight: 700;
        letter-spacing: 0; }
      @media only screen and (max-width: 31.25em) {
        .JokeList__sidebar-title {
          order: 2; } }
      @media only screen and (max-width: 24em) {
        .JokeList__sidebar-title {
          font-size: 2.5rem; } }
    .JokeList__sidebar-image {
      display: block;
      width: 50%;
      box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.6);
      border-radius: 50%;
      -webkit-transition: all 2s;
      transition: all 2s;
      margin: 3rem 0; }
      @media only screen and (max-width: 31.25em) {
        .JokeList__sidebar-image {
          width: 5rem;
          order: 1; } }
      .JokeList__sidebar-image:hover {
        -webkit-transform: rotate(360deg) scale(1.2);
                transform: rotate(360deg) scale(1.2); }
    .JokeList__sidebar-getmore {
      font-size: 1.5rem;
      cursor: pointer;
      color: white;
      font-weight: 700;
      border: none;
      outline: none;
      box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.6);
      word-spacing: 2rem;
      margin: 2rem;
      border-radius: 2rem;
      padding: 1rem 2rem;
      background: -webkit-linear-gradient(315deg, #b3e7fc 0%, #b3e7fc 50%, #f25aa3 50%, #f25aa3 100%);
      background: linear-gradient(135deg, #b3e7fc 0%, #b3e7fc 50%, #f25aa3 50%, #f25aa3 100%);
      -webkit-transition: all .5s;
      transition: all .5s; }
      .JokeList__sidebar-getmore:hover {
        -webkit-transform: translateY(-2px);
                transform: translateY(-2px);
        box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.3); }
      @media only screen and (max-width: 31.25em) {
        .JokeList__sidebar-getmore {
          order: 3;
          margin: 0; } }
      @media only screen and (max-width: 24em) {
        .JokeList__sidebar-getmore {
          font-size: 1.2rem; } }
  .JokeList__spinner {
    color: white;
    text-align: center; }

.Joke {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  padding: 1rem; }
  .Joke-buttons {
    display: flex;
    align-items: center;
    margin-right: 1rem;
    justify-content: center;
    width: 25%; }
  .Joke-text {
    width: 75%;
    font-size: 1.2rem;
    margin-right: 1rem;
    line-height: 2rem; }
    @media only screen and (max-width: 31.25em) {
      .Joke-text {
        width: 60%;
        margin-right: .6rem; } }
  .Joke-votes {
    width: 5rem;
    height: 5rem;
    line-height: 4rem;
    background-color: #fff;
    border-radius: 50%;
    border: 3px solid red;
    display: block;
    text-align: center;
    font-size: 2rem;
    font-weight: 300;
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.3); }
    @media only screen and (max-width: 31.25em) {
      .Joke-votes {
        font-size: 1.2rem;
        width: 3rem;
        height: 3rem;
        line-height: 2.5rem;
        border: 1.5px solid red;
        box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1); } }
  .Joke-smiley {
    font-size: 3rem;
    margin-left: auto;
    border-radius: 50%;
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.3); }
    @media only screen and (max-width: 24em) {
      .Joke-smiley {
        font-size: 2rem; } }

.fa-arrow-up, .fa-arrow-down {
  font-size: 1.5rem;
  margin: 1rem;
  cursor: pointer; }
  @media only screen and (max-width: 31.25em) {
    .fa-arrow-up, .fa-arrow-down {
      font-size: 2rem;
      margin: .5rem; } }

